import React from 'react'
import { useEffect } from 'react'
import { movieAction } from '../redux/actions/movieActions'
import { useDispatch, useSelector } from 'react-redux'
import Banner from '../Component/Banner'
import MovieSlide from '../Component/MovieSlide'
import { ClipLoader } from 'react-spinners'

const Home = () => {
  const dispatch = useDispatch();
  const { popularMovies, topRatedMovies, upcomingMovies ,loading} =
  useSelector((state) => state.movie);

  useEffect(() => {
    dispatch(movieAction.getMovies());
  }, []);

  // loading이 true면 loading 스피너를 보여주고 false면 데이터를 보여준다
  // true : 데이터 도착전
  // false: 데이터 도착 후

  if (loading) {
    return (
      <div className='loader'>
          <ClipLoader
          color='#fff'
          loading={loading}
          size={150}
        />
      </div>
    )
  }
  return (
    <div className='slide'>
    <Banner movies={popularMovies.results[0]} />
    <h1>Popular Movie</h1>
    <MovieSlide movies={popularMovies} />
    <h1>Top rated Movie</h1>
    <MovieSlide movies={topRatedMovies} />
    <h1>Upcoming Movie</h1>
    <MovieSlide movies={upcomingMovies} />
    </div>
  )
}

export default Home

// 단락회로평가 쓰는 이유.. 항상 return문이 먼저 렌더링이 되는데
// 영화 데이터를 끌고오기도 전에 화면이 에러가 나므로 데이터 값을 읽고 화면에 출력시키는게 먼저이기때문에 popularMovies.results && 단락회로평가를 쓴다 근데!! 데이터가 없을때 스피너를 주기로했으니깐 이제 단락회로 평가는 필요없당